<template>
  <div :style="{opacity: '0', animation: visible ? 'opacity-fade-in 0.2s forwards' : 'opacity-fade-out 0.2s forwards'}">
    <div class="shop--main">
      <p>SHOP</p>
      <div class="shop--header">
        <div class="text">
          <div class="circle"></div>
          <h1>SHOP</h1>
        </div>
        <div class="shop--categorys">
          <div :class="selectedcategory === null ? 'categoryselected' : 'category'" @click="selectcategory(null)">
            Alle
          </div>
          <div v-for="(data, index) in data.categorys" :key="index" :class="selectedcategory === data ? 'categoryselected' : 'category'" @click="selectcategory(data)">
            {{data}}
          </div>
        </div>
        <div class="shop--search">
          <i class="fal fa-search"></i>
          <input v-model="searching">
        </div>
        <div class="close" @click="close()">
          <i class="fas fa-times"></i>
        </div>
      </div>
      <div class="shop--other">
        <div class="shop--items">
          <div class="auto">
            <div class="item" v-for="(data, index) in data.items" :key="index" :style="{display: getdataforitem(index)}" @click="addProduct(index)">
              <img :src="imagePathFinder(data.name)">
              <h1>{{data.label}}</h1>
              <h2>{{data.price}} $</h2>
            </div>
          </div>
        </div>
        <div class="shop--warenkorb">
          <h1>WARENKORB</h1>
          <div class="warenkorb--items">
            <div class="auto">
              <div class="warenkorb--item" v-for="(data, index) in warenkorb" :key="index" :style="{display: getdataforkorbitem(index)}">
                <img :src="imagePathFinder(data.name)">
                <h2>{{data.label}} - <span style="color: white;">{{data.price}}$</span></h2><span style="position: relative; left: 3.8vh; color: var(--amount-color); font-size: 1.3vh;">{{data.amount}}x</span>
                <div class="remove" @click="remove(index)">
                  -1
                </div>
                <i @click="removeall(index)" class="fal fa-trash"></i>
              </div>
            </div>
          </div>
          <h1 style="color: white">PREIS: <span style="color: var(--var-pricecolor)">{{gesamtpreis()}}$</span></h1>
          <div class="buttons">
            <button @click="buy('card')">mit karte bezahlen</button>
            <button @click="buy('bar')">bar bezahlen</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shop',
  data() {
    return {
      visible: false,
      selectedcategory: null,
      searching: "",
      warenkorb: [],
      data: {}
    }
  },
  methods: {
    close() {
      this.visible = false, this.selectedcategory = null, this.warenkorb = [], this.searching = ""
      fetch("https://ays_shop/escape", {
        method: "post"
      })
    },
    imagePathFinder(file) {
      return "./img/" + file + ".png"
      // return require('@/assets/' + file + '.png')
    },
    selectcategory(Category) {
      this.selectedcategory = Category
    },
    addProduct(index) {
      const find = this.warenkorb.indexOf(this.warenkorb.find(item => item.name === this.data.items[index].name))

      if (find < 0) {
        this.warenkorb.push({name: this.data.items[index].name, label: this.data.items[index].label, amount: 1, price: this.data.items[index].price})
      } else {
        this.warenkorb[find].amount += 1
      }
    },
    remove(index) {
      if (this.warenkorb[index].amount == 1) {
        this.warenkorb.splice(index, 1)
      } else {
        this.warenkorb[index].amount -= 1
      }
    },
    removeall(index) {
      this.warenkorb.splice(index, 1)
    },
    getdataforitem(index) {
      const label = this.data.items[index].label.toUpperCase()

      if (this.selectedcategory) {
        if (this.data.items[index].category === this.selectedcategory) {
          if (this.searching != "") {
            if (label.toUpperCase().indexOf(this.searching.toUpperCase()) > -1) {
              return 'block'
            } else {
              return 'none'
            }
          } else {
            return 'block'
          }
        } else {
          return 'none'
        }
      } else {
        if (this.searching != "") {
          if (label.toUpperCase().indexOf(this.searching.toUpperCase()) > -1) {
              return 'block'
            } else {
              return 'none'
            }
        } else {
          return 'block'
        }
      }
    },
    getdataforkorbitem(index) {
      const label = this.warenkorb[index].label

      if (this.searching != "")  {
        if (label.toUpperCase().indexOf(this.searching.toUpperCase()) > -1) {
          return 'flex'
        } else {
          return 'none'
        }
      } else {
        return 'flex'
      }
    },
    buy(state) {
      fetch("https://ays_shop/buy", {
        method: "post",
        body: JSON.stringify({
          price: this.gesamtpreis(),
          korb: this.warenkorb,
          state: state
        })
      })
    },
    gesamtpreis() {
      var gesamtpreis = 0;

      for (let item of this.warenkorb) {
        gesamtpreis += item.price * item.amount
      }

      return gesamtpreis
    }
  },
  mounted() {
    window.addEventListener('message', (event) => {
      var e = event.data
      if (e.action == "show") {
        if (e.state) {
          this.visible = true
        } else {
          this.close()
        }
      } else if (e.action == "setdata") {
        this.data = JSON.parse(e.data)
      }
    });
  }
}
</script>

